import 'ScssComponents/page-nav.scss';

const pageNav = () => {
	const pageNavLinks = document.querySelectorAll('.js-nav-link');

	if (!pageNavLinks.length) return;

	pageNavLinks.forEach((link) => {
		link.addEventListener('click', (e) => {
			e.preventDefault();
			const hrefTo = e.target.getAttribute('href');

			window.scroll({
				behavior: 'smooth',
				left: 0,
				top: document.querySelector(hrefTo).offsetTop,
			});
		});
	});
};

export default pageNav;
