import Swiper, { Scrollbar, Mousewheel } from 'swiper';
import 'swiper/css';
import 'swiper/css/scrollbar';

import 'ScssComponents/scrollbar.scss';

const getSocialScrollbar = (
	sectionSelector = '.js-get-social-section',
	sliderSelector = '.js-get-social-slider',
	scrollbar = '.swiper-scrollbar',
) => {
	const $sections = document.querySelectorAll(sectionSelector);

	if (!$sections) return;

	$sections.forEach(($section) => {
		const $slider = $section.querySelector(sliderSelector);

		if (!$slider) return;

		const $slides = $slider.querySelectorAll('.swiper-slide');
		const $scrollBar = $slider.querySelector(scrollbar);

		const slidesCounter = $slides.length <= 3;

		if (slidesCounter) {
			$scrollBar.classList.add('swiper-scrollbar--small_mod');
		} else {
			$scrollBar.classList.remove('swiper-scrollbar--small_mod');
		}

		const getSocialSwiper = new Swiper($slider, {
			modules: [Scrollbar, Mousewheel],
			spaceBetween: 15,
			slidesPerView: 'auto',
			scrollbar: {
				el: `${scrollbar}`,
				draggable: true,
				dragSize: 'auto',
			},
			// mousewheel: {
			// 	enabled: true,
			// },
			forceToAxis: true,
			breakpoints: {
				640: {
					initialSlide: slidesCounter ? 1 : 0,
					spaceBetween: 20,
					centeredSlides: slidesCounter,
				},
			},
		});
	});
};

export default getSocialScrollbar;
