import Swiper, { EffectFade, Autoplay, Navigation, Pagination } from 'swiper';
import 'swiper/css/effect-fade';
import 'swiper/css';
import 'swiper/css/pagination';

const heroEvent = (
	sectionSelector = '.js-hero-event-section',
	sliderSelector = '.js-hero-event-slider',
	pagination = '.swiper-pagination',
	scrollBtn = '.js-scroll-btn',
) => {
	const $sections = document.querySelectorAll(sectionSelector);

	if (!$sections) return;
	const $scrollBtn = document.querySelector(scrollBtn);
	if (!$scrollBtn) return;

	$sections.forEach((section) => {
		const slider = section.querySelector(sliderSelector);

		if (!slider) return;

		const promoBannerSwiper = new Swiper(slider, {
			modules: [EffectFade, Autoplay, Pagination],
			effect: 'fade',
			loop: true,
			autoplay: {
				delay: 4000,
			},
			fadeEffect: {
				crossFade: true,
			},
			slidesPerView: 1,
			pagination: {
				el: pagination,
				clickable: true,
			},
		});
	});

	const scrollSections = () => {
		const sections = document.querySelectorAll('.section');
		let currentPosition = window.pageYOffset;

		for (let i = 0; i < sections.length; i += 1) {
			const section = sections[i];
			const sectionTop = section.offsetTop;
			const sectionHeight = section.offsetHeight;

			if (currentPosition >= sectionTop && currentPosition < sectionTop + sectionHeight) {
				const nextSection = sections[i + 1];

				if (nextSection) {
					const nextSectionTop = nextSection.offsetTop;

					window.scrollTo({
						top: nextSectionTop,
						behavior: 'smooth',
					});
				}

				break;
			}
		}
	};

	$scrollBtn.addEventListener('click', () => {
		scrollSections();
	});
};

export default heroEvent;
