import Swiper, { Autoplay, EffectFade, Pagination } from 'swiper';
import 'swiper/css/effect-fade';
import 'swiper/css';
import 'swiper/css/pagination';

import 'ScssComponents/banner-promo.scss';

const bannerPromo = (
	sectionSelector = '.js-banner-promo-section',
	sliderSelector = '.js-banner-promo-slider',
	pagination = '.swiper-pagination',
) => {
	const $sections = document.querySelectorAll(sectionSelector);

	if (!$sections) return;

	$sections.forEach((section) => {
		const $slider = section.querySelector(sliderSelector);

		if (!$slider) return;

		const bannerPromoSwiper = new Swiper($slider, {
			modules: [EffectFade, Autoplay, Pagination],
			effect: 'fade',
			loop: true,
			autoplay: {
				delay: 4000,
			},
			fadeEffect: {
				crossFade: true,
			},
			slidesPerView: 1,
			pagination: {
				el: pagination,
				clickable: true,
			},
		});
	});
};

export default bannerPromo;
